	},
	onReady() {
		//自定义返回按钮 因为原生的返回按钮不可阻止默认事件
		
		uni.getSystemInfo({
			success: res => {
				this.imgHeight = res.windowHeight + 'px';
			}
		});
		
		uni.onKeyboardHeightChange(res => {
			if (res.height == 0) {

				this.mpInputMargin = false;

			}else{
				this.showFunBtn = false;
			}
		});
